.selector{
    height: 400px;
    width: 5%;
    opacity: 0;
    animation: 0.6s forwards 1.5s opening ;
    display: flex;
    flex-direction: column;
    justify-content: space-between;
    align-items: center;
    margin-right: 10px;
    order: 1;

    &__arrow, i{
        cursor: pointer;
    }
    &__arrow:hover, i:hover{
        color: #16A4FA;

    }
    &__dot{
        display: flex;
        flex-direction: column;
        justify-content: space-between;
        align-items: center;
        height: 100%;
        margin-top: 50px;
        margin-bottom: 50px;
    }
}
@keyframes opening {
    from{
        opacity: 0;
    }
    to{
        opacity: 1;
    }
}

.selected{
    color: #16A4FA;
}

i{
    transition: all 0.2s ease-out;
}

@media(max-width: 980px) {
   .selector{
    height: 40px;
    width: 80%;
    margin-right: 50px;
    order: 3;
    flex-direction: row;
    margin-right: 0;
    font-size: 15px;
    animation: 0.6s forwards 2.5s opening ;

    &__dot{
        width: 100%;
        display: flex;
        flex-direction: row;
        justify-content: space-evenly;
        
    }
   }
   .right{
    rotate: -90deg;
   }
   .left{
    rotate: -90deg;
   }
}