.top_corner {
  position: absolute;
  border-top: 4px solid;
  border-left: 4px solid;
  border-color: #16A4FA;
  width: 1%;
  height: 10px;
  top: 50%;
  left: 50%;
  z-index: -1;
  transition: all 0.2s ease-in;
  visibility: none;
  animation: forwards 1.5s 0.5s openingTop;
}

.bottom_corner {
  position: absolute;
  border-bottom: 4px solid;
  border-right: 4px solid;
  border-color: #16A4FA;
  width: 1%;
  height: 10px;
  bottom: 50%;
  right: 50%;
  z-index: -1;
  transition: all 0.2s ease-in;
  animation: forwards 1.5s 0.5s openingBottom;
  white-space: nowrap;
}

@keyframes openingTop {
  0% {
    top: 50%;
    left: 50%;
    width: 1%;
    height: 10px;
    border-color: #F7F7F7;
    visibility: none;
  }
  1% {
    visibility: visible;
  }
  100% {
    top: 10%;
    left: 10%;
    width: 30%;
    height: 350px;
    border-color: #16A4FA;
  }
}
@keyframes openingBottom {
  from {
    bottom: 50%;
    right: 50%;
    width: 1%;
    height: 10px;
    border-color: #F7F7F7;
  }
  to {
    bottom: 10%;
    right: 10%;
    width: 30%;
    height: 350px;
    border-color: #16A4FA;
  }
}
h3 {
  position: absolute;
  bottom: 0;
  right: 1%;
  white-space: nowrap;
}

@media (max-width: 980px) {
  h3 {
    font-size: 1vh;
  }
}