.preview {
  position: relative;
  width: 30%;
  min-width: 280px;
  opacity: 0;
  animation: 0.6s forwards 1.9s opening;
  display: flex;
  justify-content: center;
  order: 3;
  border-radius: 5px;
}
.preview img {
  box-shadow: 8px 8px 1px 1px #151517;
}
.preview figcaption {
  position: absolute;
  bottom: -50px;
  display: flex;
  flex-direction: row;
  justify-content: center;
  width: 100%;
  padding-top: 5px;
}
.preview figcaption img {
  padding-right: 20px;
  width: 2em;
  box-shadow: none;
}

@keyframes opening {
  from {
    opacity: 0;
  }
  to {
    opacity: 1;
  }
}
.img {
  width: 100%;
  transition: all 0.2s ease-in-out;
}

.img:hover {
  cursor: pointer;
  filter: brightness(120%);
}

@media (max-width: 980px) {
  .preview {
    position: relative;
    order: 2;
    min-width: 80%;
    height: 200px;
    animation: 0.6s forwards 2.2s opening;
    margin-bottom: 10px;
  }
  .preview figcaption {
    top: -50px;
    bottom: auto;
  }
  .preview figcaption img {
    width: 1.5em;
  }
  .img {
    position: absolute;
    left: 50%;
    min-width: 220px;
    transform: translateX(-50%);
  }
}