* {
  margin: 0;
  padding: 0;
  color: #F7F7F7;
  font-family: "Rubik";
}

body {
  background-color: #1D1D20;
}

.container {
  height: 90vh;
  width: 100%;
  display: flex;
  justify-content: center;
}

.main_container {
  width: 70%;
  height: 100%;
  display: flex;
  flex-direction: column;
  align-items: center;
  justify-content: space-evenly;
}

main {
  display: flex;
  flex-direction: row;
  align-items: center;
  justify-content: space-between;
}

@media (max-width: 980px) {
  .main_container {
    height: 100vh;
    margin-top: 10vh;
    padding: 0;
    display: flex;
    flex-direction: column;
    justify-content: flex-start;
  }
  main {
    flex-direction: column;
    justify-content: space-evenly;
    height: 70vh;
    margin-top: -5%;
  }
}